import React from "react"
import { Link } from "gatsby"

const PostLink = ({ post }) => (
  <div className="post">
    <Link to={post.frontmatter.path}>
      <div className="text-2xl text-blog-primary transition-colors duration-500">{post.frontmatter.title}</div>
      <div className="text-blog-secondary text-base font-normal mt-3">{post.frontmatter.description}</div>
      <div className="text-blog-lightgray text-base font-light mt-3 mb-5 italic">wrote on {post.frontmatter.date}</div>
    </Link>
  </div>
)

export default PostLink
